.modal{
    top : 100%;
    transform : translate(-50%,-50%);
    opacity : 0;
    transition: top 0.3s ease-in-out 0.03s , opacity 0.2s ease-in-out 0s;
}
.active_modal{
    opacity: 1;
    top : 50%;
    transform: translate(-50%,-50%);
    transition: top 0.3s , opacity 0.15s ease-in-out 0.15s;
}